import { useState, useEffect } from 'react';
import howler from 'howler';

/**
 *
 * @param {string[]|number[]} [initialState=['100vw', '100vh']]
 * @return {number[]}
 */
export const useWindowDimensions = (initialState = ['100vw', '100vh']) => {
  const [size, setSize] = useState(initialState);

  useEffect(() => {
    setSize([window.innerWidth, window.innerHeight]);
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setSize([window.innerWidth, window.innerHeight]);
    };

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return size;
};

/**
 *
 * @param {string} src
 * @param {boolean} isPlaying
 * @return {Howl}
 */
export const useSound = (src, isPlaying) => {
  const [sound, setSound] = useState(/** @type Howl */undefined);
  const [time, setTime] = useState(0);
  const [loading, setLoading] = useState(undefined);

  useEffect(() => {
    console.log(`loading ${src}`);
    setLoading(true);
    /** @type Howl */
    const s = new howler.Howl({
      src: [`assets/${src}.mp3`],
      autoUnlock: true,
      // preload: true,
      html5: true, // needed for streaming
      onload: () => {
        setLoading(false);
        console.log(`${src} loaded`);
        console.log(`starting at ${time}s`);
        s.seek(time);
      },
      onloaderror: (e) => {
        console.error(src);
        console.error(e);
      },
      onplayerror: (e) => {
        console.error(src);
        console.error(e);
      }
    });

    setSound(s);

    return () => {
      console.log(`${src} unloaded`);
      s.unload();
    };
  }, [src]);


  useEffect(() => {
    if (!sound) { return; }
    if (isPlaying) {
      sound.play();
    } else {
      sound.pause();
    }
  }, [sound, isPlaying]);

  useEffect(() => {
    if (!sound) { return undefined; }

    const t = setInterval(() => {
      setTime(sound.seek() || 0);
    }, 100);

    return () => {
      clearInterval(t);
    };
  }, [sound]);

  return [sound, time, loading];
};

/**
 *
 * @param {string} name
 * @param {number} [totalChunks]
 * @param {string} [ext=mp3]
 * @param {function} [onend]
 * @return {Howl[]}
 */
export const createPlaylist = (name, totalChunks = 3, ext = 'mp3', onend) => {
  const streams = [];

  for (let i = 1; i <= totalChunks; i += 1) {
    streams[i] = new howler.Howl({
      src: [`assets/audio/${name}.act${i}.${ext}`],
      autoUnlock: true,
      // preload: false,
      html5: true, // needed for streaming
      onend,
      onload: () => {
        console.log(`loaded ${name} part ${i}`);
      },
      onloaderror: (e) => {
        console.error(name);
        console.error(e);
      },
      onplayerror: (e) => {
        console.error(name);
        console.error(e);
      }
    });
  }

  return streams;
};
